import type { Amount, ParseMoneyFunction, ParseLocalizedMoneyFunction } from './types';

/**
 * Parse given amount as float or return as is if already a float
 */
export const parseMoney: ParseMoneyFunction = (amount: Amount): number => {
	if (typeof amount === 'number') return amount;
	const float = Number.parseFloat(amount);
	if (Number.isNaN(float)) return 0;
	return float;
};

/**
 * Curried function that removes localized formatting and returns a float
 */
export const parseLocalizedMoney: ParseLocalizedMoneyFunction =
	(decimalMark = '.', thousandsSeparator = ','): ParseMoneyFunction =>
		(amount: string): number => {
		// remove thousands separators
		let normalized = amount.replace(new RegExp('\\' + thousandsSeparator, 'g'), '');
		// Replace decimal mark with '.'
		if (decimalMark !== '.') {
			normalized = normalized.replace(decimalMark, '.');
		}
		return parseFloat(normalized);
	};
